import React from 'react'

import Map from 'ol/Map.js';
import View from 'ol/View.js';
import MVT from 'ol/format/MVT.js';
import VectorTileLayer from 'ol/layer/VectorTile.js';
import VectorTileSource from 'ol/source/VectorTile.js';
import {Fill, Stroke, Style} from 'ol/style.js';

import './Map.css';

let state = {
      selection : {},
      idProp : 'iso_a3'
    };

class MapOL extends React.Component {

  constructor(props: Props) {
    super(props);
  }

  componentDidMount() {
    var vtLayer = new VectorTileLayer({
      declutter: true,
      source: new VectorTileSource({
        format: new MVT(),
        url: 'https://ahocevar.com/geoserver/gwc/service/tms/1.0.0/' +
          'ne:ne_10m_admin_0_countries@EPSG%3A900913@pbf/{z}/{x}/{-y}.pbf'
      }),
      style: function(feature) {
        var selected = !!(state.selection[feature.get(state.idProp)]);
        return new Style({
          stroke: new Stroke({
            color: selected ? 'grey' : 'gray',
            width: selected ? 1 : 1
          }),
          fill: new Fill({
            color: selected ? 'blue' : 'rgba(20,20,20,0.9)'
          })
        });
      }
    });

    var map = new Map({
      layers: [
        vtLayer
      ],
      target: this.mapContainer,
      view: new View({
        center: [0, 0],
        zoom: 2
      })
    });

          map.on('click', function(event) {
        var features = map.getFeaturesAtPixel(event.pixel);
        if (!features) {
          state.selection = {};
          // force redraw of layer style
          vtLayer.setStyle(vtLayer.getStyle());
          return;
        }
        var feature = features[0];
        var fid = feature.get(state.idProp);

        state.selection = {};

        // add selected feature to lookup
        state.selection[fid] = feature;

        // force redraw of layer style
        vtLayer.setStyle(vtLayer.getStyle());
      });
}

  render() {
    return (
      <div ref={el => this.mapContainer = el} className='Map' />
    );
  }
}

export default MapOL